import Head from "../../components/head";
import Humanize from "../../components/humanize";
import Icon from "../../components/icon";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import Truncate from "../../components/truncate";
import { makeSearchUrl } from "./../search/page";
import { useSearchParams } from "react-router-dom";

export default function PackageHeader(props) {
  const [searchParams] = useSearchParams();
  const customVersion = searchParams.get("version");
  const byOwner = getByOwner(props.data.owner);
  return (
    <>
      <Head
        title={props.data.name + " by " + byOwner}
        description={props.data.description}
        noindex={!!customVersion}
      />
      <div className="hstack flex-wrap gap-2">
        <div>
          <h1 className="h2">
            {props.data.owner.username}/{props.data.name}
          </h1>
        </div>
        <div>
          <small className="h5 me-2">
            <span className="text-muted me-1">by</span>
            <Link
              to={makeSearchUrl({
                query: { owner: props.data.owner.username },
              })}
            >
              {byOwner}
            </Link>
          </small>
        </div>
        <div>
          <sup className="h5">
            <span className="badge bg-secondary text-uppercase me-2">
              <div className="d-flex flex-row align-items-center">
                <div>
                  <Icon name={`package-${props.data.type}`} />
                </div>
                <div className="ps-2">{props.data.type}</div>
              </div>
            </span>
            {/* <span className="badge bg-success me-2">
              <div className="d-flex flex-row align-items-center">
                <div>
                  <Icon name="shield-check" />
                </div>
                <div className="ps-2">OFFICIAL</div>
              </div>
            </span> */}
            {props.data.popularity_rank <= 100 ? (
              <span className="badge bg-danger me-2">
                <div className="d-flex flex-row align-items-center">
                  <div>
                    <Icon name="heart" />
                  </div>
                  <div className="ps-2">TOP 100</div>
                </div>
              </span>
            ) : (
              ""
            )}
          </sup>
        </div>
      </div>
      <ul className="list-inline text-muted">
        <li className="list-inline-item">
          <Humanize type="original-version" value={props.data.version.name} />
        </li>
        <li className="list-inline-item"></li>
        <li className="list-inline-item text-success">Public</li>
        <li className="list-inline-item"></li>
        <li className="list-inline-item">
          Published{" "}
          <Humanize type="time" value={props.data.version.released_at} />
        </li>
        {props.data.keywords ? (
          <>
            <li className="list-inline-item"></li>
            <li className="list-inline-item">
              <Truncate
                value={props.data.keywords.map((item) => (
                  <span
                    key={item}
                    className="badge rounded-pill text-primary bg-info me-2"
                  >
                    <Link
                      key={item}
                      to={makeSearchUrl({ query: { keyword: item } })}
                    >
                      {item}
                    </Link>
                  </span>
                ))}
                max={5}
                moreTitle={`+ ${props.data.keywords.length - 5} ${
                  props.data.keywords.length - 5 === 1
                    ? "category"
                    : "categories"
                }`}
              />
            </li>
          </>
        ) : (
          ""
        )}
      </ul>
      <p className="mb-4 text-break">
        <Truncate
          value={props.data.description}
          max={255}
          moreTitle="Read more "
        />
      </p>
    </>
  );
}

PackageHeader.propTypes = {
  data: PropTypes.shape({
    type: PropTypes.string.isRequired,
    owner: PropTypes.shape({
      username: PropTypes.string.isRequired,
    }).isRequired,
    name: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    version: PropTypes.shape({
      name: PropTypes.string.isRequired,
      released_at: PropTypes.string.isRequired,
      is_latest: PropTypes.bool.isRequired,
    }).isRequired,
    keywords: PropTypes.arrayOf(PropTypes.string),
    popularity_rank: PropTypes.number.isRequired,
  }).isRequired,
};

function getByOwner(owner) {
  let by = owner.firstname || "";
  if (owner.lastname) {
    by += (by ? " " : "") + owner.lastname;
  }
  return by || owner.username;
}
