import { getSystemMap, useFrameworks, usePlatforms } from "../../data";
import Icon from "../../components/icon";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import Spinner from "../../components/spinner";
import { makeSearchUrl } from "./../search/page";
import { valueCmp } from "../../helpers";

export default function PackageCompatibility(props) {
  return (
    <>
      <h2 className="h3">Compatibility</h2>
      {props.data.type === "library" ? (
        <PackageFrameworkCompatibility data={props.data} />
      ) : props.data.type === "tool" ? (
        <PackageToolCompatibility data={props.data} />
      ) : (
        "Platform"
      )}
    </>
  );
}

PackageCompatibility.propTypes = {
  data: PropTypes.shape({
    type: PropTypes.string.isRequired,
  }).isRequired,
};

function PackageFrameworkCompatibility(props) {
  const allFrameworks = useFrameworks().data;
  const allPlatforms = usePlatforms().data;
  if (!allFrameworks || !allPlatforms) return <Spinner />;
  const getTitle = (items, name) => {
    const item = items.find((item) => item.name === name);
    return item ? item.title : name;
  };

  return (
    <div className="row">
      <div className="col">
        <table className="table table-hover">
          <thead>
            <tr>
              <th scope="col">Framework</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {!(props.data.frameworks || []).length ? (
              <tr>
                <td colSpan="2">No information</td>
              </tr>
            ) : props.data.frameworks.includes("*") ? (
              <tr>
                <td className="w-100">
                  <Link
                    to={makeSearchUrl({
                      query: { keyword: "framework" },
                      type: "tool",
                    })}
                  >
                    Any supported framework
                  </Link>
                </td>
                <td className="text-success">
                  <Icon name="check-circle" size="20" />
                </td>
              </tr>
            ) : (
              props.data.frameworks.sort(valueCmp).map((name) => (
                <tr className="align-middle">
                  <td className="w-100">
                    <Link
                      to={makeSearchUrl({
                        query: name,
                        type: "tool",
                      })}
                    >
                      {getTitle(allFrameworks, name)}
                    </Link>
                  </td>
                  <td className="text-success">
                    <Icon name="check-circle" size="20" />
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
      <div className="col">
        <table className="table table-hover">
          <thead>
            <tr>
              <th scope="col">Platform</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            {!(props.data.platforms || []).length ? (
              <tr>
                <td colSpan="2">No information</td>
              </tr>
            ) : props.data.platforms.includes("*") ? (
              <tr>
                <td className="w-100">
                  <Link
                    to={makeSearchUrl({
                      type: "platform",
                    })}
                  >
                    Any supported platform
                  </Link>
                </td>
                <td className="text-success">
                  <Icon name="check-circle" size="20" />
                </td>
              </tr>
            ) : (
              props.data.platforms.sort(valueCmp).map((name) => (
                <tr className="align-middle">
                  <td className="w-100">
                    <Link
                      to={makeSearchUrl({
                        query: name,
                        type: "platform",
                      })}
                    >
                      {getTitle(allPlatforms, name)}
                    </Link>
                  </td>
                  <td className="text-success">
                    <Icon name="check-circle" size="20" />
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}

PackageFrameworkCompatibility.propTypes = {
  data: PropTypes.shape({
    frameworks: PropTypes.arrayOf(PropTypes.string),
    platforms: PropTypes.arrayOf(PropTypes.string),
  }).isRequired,
};

function PackageToolCompatibility(props) {
  const allSystems = getSystemMap();
  const allArchs = [];
  const allOSes = [];
  Object.values(allSystems).forEach((item) => {
    if (!allArchs.includes(item.arch)) allArchs.push(item.arch);
    if (!allOSes.includes(item.os)) allOSes.push(item.os);
  });
  let compatibleSystems = [];
  props.data.version.files.forEach((file) => {
    if (!file.system) return;
    compatibleSystems = [
      ...compatibleSystems,
      ...(file.system === "*" ? ["*"] : file.system),
    ];
  });
  if (compatibleSystems.includes("windows")) {
    compatibleSystems = [
      ...compatibleSystems,
      ...["windows_x86", "windows_amd64"],
    ];
  }
  if (!compatibleSystems.length) return "No information";

  const isCompat = (os, arch) => {
    if (compatibleSystems.includes("*")) return true;
    for (const [system, data] of Object.entries(allSystems)) {
      if (
        data.os === os &&
        data.arch === arch &&
        compatibleSystems.includes(system)
      )
        return true;
    }
    return false;
  };
  const listCompatSystems = (os, arch) => {
    const result = [];
    for (const [system, data] of Object.entries(allSystems)) {
      if (
        data.os === os &&
        data.arch === arch &&
        (compatibleSystems.includes("*") || compatibleSystems.includes(system))
      )
        result.push(system);
    }
    return result;
  };

  return (
    <table className="table table-hover w-100">
      <thead>
        <tr align="center">
          <th scope="col"></th>
          {allArchs.map((arch) => (
            <th key={arch} scope="col">
              {arch}
            </th>
          ))}
        </tr>
      </thead>
      <tbody>
        {allOSes.map((os) => (
          <tr key={os}>
            <th>{os}</th>
            {allArchs.map((arch) =>
              isCompat(os, arch) ? (
                <td
                  key={`${os}-${arch}`}
                  className="text-success"
                  align="center"
                  data-bs-toggle="tooltip"
                  title={listCompatSystems(os, arch).join(", ")}
                >
                  <Icon name="check-circle" size="20" />
                </td>
              ) : (
                <td
                  key={`${os}-${arch}`}
                  className="text-secondary"
                  align="center"
                >
                  <Icon name="x" size="20" />
                </td>
              )
            )}
          </tr>
        ))}
      </tbody>
    </table>
  );
}

PackageToolCompatibility.propTypes = {
  data: PropTypes.shape({
    version: PropTypes.shape({
      files: PropTypes.arrayOf(
        PropTypes.shape({
          system: PropTypes.arrayOf(PropTypes.string),
        })
      ),
    }),
  }).isRequired,
};
