import { Link, useSearchParams } from "react-router-dom";
import { capitalize } from "../../helpers";
import { pkgTypeToPlural } from "../../data";

export default function SearchTypeButton() {
  const [searchParams] = useSearchParams();
  const currentType = searchParams.get("t") || "all";
  const _makeTypeUrl = (type) => {
    const qs = new URLSearchParams(searchParams);
    ["p", "t"].forEach((key) => qs.delete(key));
    if (type) qs.set("t", type);
    return "/search?" + qs.toString();
  };

  return (
    <>
      <button
        className="btn btn-outline-secondary dropdown-toggle"
        type="button"
        id="searchTypeMenu"
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        {currentType === "all"
          ? "All"
          : capitalize(pkgTypeToPlural(currentType))}
      </button>

      <ul
        className="dropdown-menu dropdown-menu-dark"
        aria-labelledby="searchTypeMenu"
      >
        {["all", "library", "platform", "tool"].map((type) => (
          <li key={type}>
            <Link
              className={`dropdown-item ${
                currentType === type ? "active" : ""
              }`}
              to={_makeTypeUrl(type === "all" ? undefined : type)}
            >
              {type === "all" ? "All" : capitalize(pkgTypeToPlural(type))}
            </Link>
          </li>
        ))}
      </ul>
    </>
  );
}
